<template>
  <div class="inner-content">
    <AntTitle>{{ errorInfo.title }} </AntTitle>
    <AntText class="message">{{ errorInfo.message }}</AntText>
    <AntButton v-if="errorInfo.action" type="link" @click="goToHome">{{
      errorInfo.action
    }}</AntButton>
    <AntCard v-if="errorInfo.showAd" :bordered="false" class="card">
      <div class="card-content">
        <AbstraLogo style="margin-bottom: 10px" />
        <AntText
          >This page is part of {{ projectName ? `the ${projectName}` : 'a' }} workflow, built with
          Abstra.</AntText
        >
        <AntText v-if="workspaceStore.state.workspace"
          >Automate your own processes by getting started
          <AntLink :href="ABSTRA_URLS.HOMEPAGE">here</AntLink>.</AntText
        >
      </div>
    </AntCard>
  </div>
</template>

<script lang="ts" setup>
import AbstraLogo from '@/components/AbstraLogo.vue';
import { ABSTRA_URLS } from '@/utils/constants';
import { i18nProvider } from '@/widgets-lib/common/i18n';
import {
  Button as AntButton,
  Card as AntCard,
  TypographyLink as AntLink,
  Typography as AntText,
  TypographyTitle as AntTitle,
} from 'ant-design-vue';
import { computed } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useWorkspaceStore } from '../store/workspaceStore';

const route = useRoute();
const router = useRouter();

const workspaceStore = useWorkspaceStore();

const projectName = computed(
  () => workspaceStore.state.workspace?.name ?? workspaceStore.state.workspace?.brandName,
);

const errorInfo = computed(() => {
  const { status } = route.params;

  switch (status) {
    case '404':
      return {
        status,
        title: i18nProvider.translate('i18n_page_not_found'),
        message: i18nProvider.translate('i18n_page_not_found_message'),
        showAd: false,
      };
    case '403':
      return {
        status,
        title: i18nProvider.translate('i18n_access_denied'),
        message: i18nProvider.translate('i18n_access_denied_message'),
        action: 'Go back to home',
        showAd: true,
      };
    default:
      return {
        status: '500',
        title: i18nProvider.translate('i18n_internal_error'),
        message: i18nProvider.translate('i18n_internal_error_message'),
        showAd: false,
      };
  }
});

const goToHome = () => {
  router.push({ name: 'playerHome' });
};
</script>

<style lang="scss" scoped>
.inner-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  position: relative;

  .message {
    max-width: 500px;
    text-align: center;
    font-size: 16px;
    margin-bottom: 40px;
  }

  .card {
    margin-top: 40px;
    .card-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
}
</style>
